const { jQuery: $ } = window;

require( '@fonticonpicker/fonticonpicker' )( jQuery );
// console.log( fip );
const fipInput = $( '#e14_element' ).fontIconPicker( {
	theme:     'fip-darkgrey',
	emptyIcon: false,
})
;

// Add the event on the button
$( '#e14_buttons button' ).on( 'click', function( e ) {
	// Append the fontawesome CDN
	if ( !$( '#fontawesome-cdn' ).length ) {
		$( 'head' ).append( '<link rel="stylesheet" href="https://use.fontawesome.com/releases/v5.8.1/css/all.css" integrity="sha384-50oBUHEmvpQ+1lW4y57PTFmhCaXp0ML5d60M1M7uH2+nqUivzIebhndOJK28anvf" crossorigin="anonymous">' );
	}
	// Prevent default
	e.preventDefault();
	// Show processing message
	$( this ).prop( 'disabled', true )
		.html( '<i class="icon-cog demo-animate-spin"></i> Please waitâ€¦' );
	// Get the JSON file
	$.ajax( {
		url:      'http://sandbox.connections-pro.com/wp-content/plugins/connections/assets/font-icon-maps/fontawesome/fontawesome-rs-categorized.json',
		type:     'GET',
		dataType: 'json'
	} )
		.done( function( response ) {
			console.log( response );
			setTimeout( function() {
				// Reset icons
				fipInput.setIcons( response );

				// Show success message and disable
				$( '#e14_buttons button' )
					.removeClass( 'btn-primary' )
					.addClass( 'btn-success' )
					.text( 'Successfully loaded icons' )
					.prop( 'disabled', true );
			}, 1000 );
		} )
		.fail( function() {
			// Show error message and enable
			$( '#e14_buttons button' )
				.removeClass( 'btn-primary' )
				.addClass( 'btn-danger' )
				.text( 'Error: Try Again?' )
				.prop( 'disabled', false );
		} );
	e.stopPropagation();
} );
